@import "../../scss/variables";


.colorPanel {
    margin: 0px;
    padding: 5px;
    position: fixed;
    z-index: 100;
    min-width: 20px;
    background-color: #2c3e50;
    top: 40%;
  
    ul {
      margin: 0px;
      padding: 0px;
      list-style: none;
      display: none;
  
      li {
        display: block;
        margin-top: 5px;
      }
  
      a {
        display: block;
        width: 20px;
        height: 20px;
        border: #95a5a6 1px solid;
      }
    }
  }
  
  #cpToggle {
    display: block;
    height: 20px;
    width: 20px;
    background-image: url(data:image/png;base64,);
    background-size: cover;
  }